.section_divisors{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  
    
}

.images_container img{
    max-width: 10em;
    max-height: 10em;
    border-radius: 50%;
    border-style: solid;
    border-color: rgb(255, 255, 255);
    margin-left: 2em;
}
.top{
    padding-bottom: 2em;
}

.experience_container{
    padding-left: 10em;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.experience_container ul{
    list-style-type: square;
    color: rgb(255, 255, 255);
}
.experience_container h1{
    color: rgb(255, 0, 0);
    font-size: 3em;
    font-weight: 400;
    margin-top: 1em;
    margin-bottom: .2em;
}
.experience_container h2{
    font-size: 1.2em;
}
.experience_container p{
    font-size: 1em;
}
.experience_container li{
    margin-bottom: 1em;
}